import Vue from "vue";
import registerServiceWorker from "./registerServiceWorker.js";
registerServiceWorker("./sw.js");
import basicComponents from "./components.js";
Vue.use(basicComponents)
/**
 * @file main
 * @author mars
 */

/* globals Vue */

import App from './MarsApp.vue';
import router from './router.js';
import initGlobalApi from './globalApi.js';
import './mars-base.css'; // 引入公共样式
import appApi from './appApi.js';
Vue.config.productionTip = false;

let marsAppData = appApi;
window.getApp = function () {
  return marsAppData;
};
Vue.prototype.currentRoute = [];

Vue.prototype.$mpUpdated = function (fn) {
  return this.$nextTick(fn);
};

window.getCurrentPages = function () {
  return Vue.prototype.currentRoute;
};
const vm = new Vue({
  router,
  data: {
    pageTitleMap: {
      "/app": {
        title: "",
        enablePullDownRefresh: false,
        enableReachBottom: false,
        navigationBarBackgroundColor: "",
        navigationBarTextStyle: "",
        backgroundColor: "",
        backgroundTextStyle: "",
        navigationStyle: ""
      },
      "/pages/home/index": {
        title: "Mars",
        enablePullDownRefresh: false,
        enableReachBottom: false,
        navigationBarBackgroundColor: "",
        navigationBarTextStyle: "",
        backgroundColor: "",
        backgroundTextStyle: "",
        navigationStyle: ""
      },
      "/pages/second/index": {
        title: "Full House Page Title",
        enablePullDownRefresh: false,
        enableReachBottom: false,
        navigationBarBackgroundColor: "",
        navigationBarTextStyle: "",
        backgroundColor: "",
        backgroundTextStyle: "",
        navigationStyle: ""
      }
    }
  },
  render: h => h(App, {
    props: {
      tabBars: [],
      navigationBarBackgroundColor: "#3eaf7c",
      navigationBarTextStyle: "white",
      useTransition: true,
      navigationBarHomeColor: "light",
      showNavigationBorder: true,
      useTransition: true,
      homePage: "/pages/home/index"
    }
  })
}).$mount('#app');

// 初始化 $api
initGlobalApi(Vue, vm);

// 将初始化后的$api赋值给getApp
marsAppData.$api = vm.$api;